// ** Redux Imports
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'

// ** Axios Imports
import axios from '@src/configs/axios/axiosConfig'

export const getData = createAsyncThunk('appAttendance/getData', async params => {
  const response = await axios.post('/taskreport/listbyclients', params)
  return {
    params,
    data: response.data.result.clients,
    total: response.data.result.total
  }
})

export const exportExcelList = createAsyncThunk('appUsers/exportExcelList', async params => {
  const response = await axios.post(`/clients/exportToExcel`, params)

  const ws = XLSX.utils.json_to_sheet(response.data.clients)
  const wb = { Sheets: { data: ws }, SheetNames: ["data"] }
  const excelBuffer = XLSX.write(wb, { bookType: "xlsx", type: "array" })
  const data = new Blob([excelBuffer], { type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8" })
  FileSaver.saveAs(data, 'client.xlsx')

})


export const appAttendanceSlice = createSlice({
  name: 'appAttendance',
  initialState: {
    data: [],
    monthlySummaries: [],
    total: 1,
    params: {}
  },
  reducers: {},
  extraReducers: builder => {
    builder
      .addCase(getData.fulfilled, (state, action) => {
        state.data = action.payload.data
        state.params = action.payload.params
        state.total = action.payload.totalPages
      })
  }
})

export default appAttendanceSlice.reducer
